/* header
----------------------------------------- */
@use "../global" as *;

body {
  &.subpage {
    .l-header {
      background: #fff;
      opacity: 1;
      transform: translateY(0);

      @include responsive(lg2) {
        height: rem(118);
      }
    }

    .l-header__logoLink {
      display: none;
    }

    .l-header__logoLink--subpage {
      display: block;

      & figure {
        picture {
          img {
            width: 100%;
          }
        }
      }
    }

    .l-header__button {
      @include responsive(lg2) {
        top: rem(38);
        right: rem(122);
      }
    }
  }
}

body.is-active {
  height: 100%;
  overflow: hidden;
}

/*==============================================================
CSS
==============================================================*/
.l-header {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 7;
  width: 100%;
  height: 60px;
  padding: rem(10);
  @include flex(space-between, center, row);
  opacity: 1;

  @include responsive(lg2) {
    padding: rem(26) rem(30);
  }
  &.js-view {
    opacity: 1;
  }
  &.js-hide {
    opacity: 0;
  }
}

.l-header__inner {
  width: 100%;

  @include flex(space-between, center, row);
}

.l-header__logo {
  position: relative;
  z-index: 101;
  width: rem(151);
  max-width: 251px;

  @include responsive(lg2) {
    width: rem(251);
    position: absolute;
    top: rem(25);
    left: rem(32);
  }

  &.-sub {
    display: none;
    z-index: 101;
  }
}

.l-header__logo {
  .l-header__logoLink {
    figure {
      picture {
        img {
          width: 100%;
        }
      }
    }
  }
}

.l-header__logoLink--subpage {
  display: none;
}

.l-header__right {
  @include responsive(lg2) {
    width: 50%;
  }
}

.l-header__button {
  display: none;

  @include responsive(lg2) {
    display: flex;
    position: absolute;
    top: rem(38);
    right: rem(122);
    z-index: 101;
  }
}

.l-header__buttonLink {
  @include responsive(lg2) {
    display: flex !important;
    justify-content: center;
    align-items: center;
    color: #fff;
    padding: rem(14) 0;
    width: rem(126);
  }

  &.-recruit {
    background: $color-text;
    font-family: $base-font-family;
    font-size: rem(14);
    font-weight: 700;
    letter-spacing: 0em;
    line-height: calc(14 / 14);
    margin-right: rem(22);
  }

  &.-entry {
    background: $color-sub;
    font-family: $second-font-family;
    font-size: rem(14);
    font-weight: 400;
    letter-spacing: 0em;
    line-height: calc(14 / 14);
    transition: 0.3s ease-in-out;
  }

  &.-entry:hover {
    background: $color-accent;
  }
}

.is-active--none {
  display: none;
}

.is-active--visible {
  display: block !important;
}

/*==============================================================

==============================================================*/
.l-header {
  &.-sub {
    background: #fff;
    opacity: 1;
    z-index: 1000;

    @include responsive(lg2) {
      height: rem(118);
    }

    .l-header__logoLink {
      display: none;
    }

    .l-header__logoLink--subpage {
      display: block;

      & figure {
        picture {
          img {
            width: 100%;
          }
        }
      }
    }

    .l-header__button {
      @include responsive(lg2) {
        top: rem(38);
        right: rem(122);
      }
    }
  }
}

.fixed-header {
  position: fixed;
  top: -80px;
  left: 0;
  z-index: 1;
  transition: all 0.3s ease-in-out;
  visibility: hidden;
}

/*==============================================================
CSS
==============================================================*/
.fixed-header.is-show {
  top: 0;
  visibility: visible;
}
