/* p-entry-thanks
----------------------------------------- */
@use "../../global" as *;

/*==============================================================
.p-entry
==============================================================*/

body.entry-thanks {
    .c-breadcrumbs {
        margin-top: rem(60);

        @include responsive(lg2) {
            margin-top: rem(118);
        }
    }

    .l-section__subTitle {
        margin-top: rem(16);

        @include responsive(md) {
            margin-top: rem(29);
        }
    }
}

.p-entry-thanks {
    background: $color-background;

    &__wrapper {
        background: #fff;
        border-radius: 11px;
        padding: rem(30) rem(15);

        @include responsive(sm) {
            padding: rem(27) rem(71) rem(95);
        }
    }

    &__headTitle {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(14);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(14 / 14);
        text-align: center;

        @include responsive(md) {
            font-size: rem(24);
            line-height: calc(24 / 24);
        }
    }

    &__attention {
        text-align: center;
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(11);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(16 / 11);
        margin-top: rem(30);

        @include responsive(md) {
            font-size: rem(16);
            line-height: calc(24 / 16);
            margin-top: rem(52);
        }
    }
}
