/* form
----------------------------------------- */
@use "../global" as *;

.l-form {
    border-top: 1px dashed $color-accent;
    margin-top: rem(68);

    @include responsive(md) {
        margin-top: rem(93);
    }

    &__list {
        padding-top: rem(50);

        @include responsive(md) {
            padding-top: rem(97);
        }
    }

    &__item {
        @include responsive(md) {
            display: flex;
            justify-content: space-between;
            align-items: baseline;
        }

        &.-radio {
            @include responsive(md) {
                margin-top: rem(42) !important;
                align-items: flex-start;
            }
        }

        &.-radio-sec {
            @include responsive(md) {
                align-items: flex-start;
            }
        }

        &.-message {
            @include responsive(md) {
                align-items: flex-start;
            }

            .l-form__dt {
                @include responsive(md) {
                    padding-top: rem(16);
                }
            }
        }
    }

    &__item:not(:first-child) {
        margin-top: rem(20);

        @include responsive(md) {
            margin-top: rem(27);
        }
    }

    &__inputWrapper {
        display: flex;
        align-items: center;
        margin-top: rem(20);

        @include responsive(md) {
            margin-top: 0;
        }

        .wpcf7-form-control-wrap {
            input {
                @include responsive(sm) {
                    width: rem(156);
                }
                @include responsive(md) {
                    width: rem(168) !important;
                }
            }
        }

        &.-select {
            position: relative;

            &::before {
                content: "";
                display: inline-block;
                vertical-align: middle;
                color: #333;
                line-height: 1;
                width: 9px;
                height: 9px;
                border: 2px solid currentColor;
                border-left: 0;
                border-bottom: 0;
                box-sizing: border-box;
                transform: translateY(-47%) rotate(135deg);
                position: absolute;
                top: 47%;
                right: rem(43);
                top: 47%;
                z-index: 2;
            }
            .wpcf7-form-control-wrap {
                width: 100%;

                @include responsive(lg2) {
                    width: 100%;
                }
            }
        }
        .wpcf7-form-control-wrap {
            width: 100%;

            @include responsive(sm) {
                width: initial;
            }
        }
    }

    &__padding {
        padding-left: rem(5);
        @include responsive(md) {
            padding-left: rem(10);
        }
    }

    &__birthday--bottom {
        @include responsive(sm) {
            display: flex;
            margin-top: rem(15);
        }

        .l-form__inputWrapper:first-of-type {
            @include responsive(md) {
                margin-right: rem(24);
            }
        }
    }

    &__dt {
        color: #000;
        font-family: $base-font-family;
        font-size: rem(14);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(23.17 / 16);

        @include responsive(md) {
            font-size: rem(16);
            width: rem(128);
        }

        &--accent {
            color: $color-accent;
        }

        &.-pt {
            @include responsive(md) {
                padding-top: rem(12);
            }
        }
    }

    &__dd {
        margin-top: rem(19);
        max-width: 575px;
        @include responsive(md) {
            width: rem(575);
            margin-top: 0;
        }
        div {
            input {
                background: $color-background;
                border: 1px solid $color-accent;
                border-radius: 10px;
                width: 100%;
                height: rem(56);
                padding: 0 rem(20);
            }
        }

        span {
            input {
                background: $color-background;
                border: 1px solid $color-sub;
                border-radius: 10px;
                width: 100%;
                height: rem(56);
                padding: 0 rem(20);
            }
        }

        select {
            -webkit-appearance: none;
            appearance: none;
            background: $color-background;
            border: 1px solid $color-sub;
            border-radius: 10px;
            color: #000;
            font-size: rem(12);
            width: 100%;
            height: rem(56);
            padding: 0 rem(20);

            @include responsive(sm) {
                width: rem(156);
            }

            @include responsive(md) {
                width: rem(168) !important;
            }
        }

        &.-radio {
            .wpcf7-form-control-wrap {
                .wpcf7-form-control {
                    display: flex;
                    flex-direction: column;
                }
            }

            & span {
                margin: 0;
            }

            & span:not(:first-of-type) {
                margin: rem(16) 0 0 0;
            }

            .wpcf7-list-item {
                label {
                    display: initial;
                    align-items: center;
                    justify-content: flex-start;
                }
            }
        }

        &.-radio-sec {
            .wpcf7-form-control-wrap {
                .wpcf7-form-control {
                    display: flex;
                    flex-direction: column;
                    @include responsive(md) {
                        margin-top: rem(11);
                    }

                    & span {
                        margin: 0;
                    }

                    & span:not(:first-of-type) {
                        margin: rem(21) 0 0 0;
                    }
                    .wpcf7-list-item {
                        label {
                            display: flex;
                            align-items: center;
                            justify-content: flex-start;

                            input[type="radio"] {
                                border-radius: 0%;
                            }

                            input[type="radio"]:before {
                                border-radius: 0%;
                            }
                        }
                    }
                }
            }
        }
        .wpcf7-list-item-label {
            font-family: $base-font-family;
            font-size: rem(14);
            font-weight: 400;
            letter-spacing: 0em;
            line-height: calc(20 / 14);
            padding-left: 0;

            @include responsive(md) {
                font-size: rem(16);
                line-height: calc(23.17 / 16);
            }
        }
        .wpcf7-form-control-wrap {
            .wpcf7-form-control {
                .wpcf7-list-item {
                    @include responsive(md) {
                        justify-content: flex-start;
                    }
                }
            }
        }

        textarea {
            background: $color-background;
            border: 1px solid $color-sub;
            border-radius: 10px;
            width: 100%;
            height: rem(240);
            padding: rem(18) rem(19);
        }

        //
        input[type="radio"] {
            -webkit-appearance: none; /*  */
            width: rem(24);
            height: rem(24);
            border: 1px solid $color-sub;
            border-radius: 50%;
            padding: 0;
            margin-right: rem(12);
            position: relative;
            cursor: pointer;
        }
        /*  */
        input[type="radio"]:before {
            content: "";
            display: block;
            width: rem(10);
            height: rem(10);
            border-radius: 50%;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
        /*  */
        input[type="radio"]:checked:before {
            background: $color-text;
            border: 1px solid $color-text;
        }
    }

    &__input--square {
        input[type="radio"] {
            border-radius: 0%;
        }

        input[type="radio"]:before {
            border-radius: 0%;
        }
    }
    //

    &__label {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(16);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(23.17 / 16);
    }

    &__privacy {
        display: block;
        margin-top: rem(32);
        text-align: center;

        @include responsive(md) {
            margin-top: rem(100);
        }
    }

    &__privacy input[type="checkbox"] {
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        background: $color-background;
        border: 1px solid $color-sub;
        border-radius: 0%;
        width: rem(16);
        height: rem(16);
        cursor: pointer;
        position: relative;
    }

    &__privacy input[type="checkbox"]:checked:before {
        position: absolute;
        top: 1px;
        left: 5px;
        transform: rotate(50deg);
        width: 4px;
        height: 11px;
        border-right: 2px solid $color-accent;
        border-bottom: 2px solid $color-accent;
        content: "";
    }

    &__privacyText {
        display: inline-block;
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(14);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(14 / 14);
        padding-left: rem(6);

        a {
            border-bottom: 2px solid $color-text;
        }
    }

    &__submit {
        text-align: center;
        margin-top: rem(36);

        @include responsive(md) {
            margin-top: rem(61);
        }
    }
}

.wpcf7-submit {
    display: block;
    cursor: pointer;
    background: #fff;
    border: 1px solid $color-text !important;
    color: $color-text;
    font-family: $base-font-family;
    font-size: rem(16);
    font-weight: 400;
    letter-spacing: 0em;
    line-height: calc(16 / 16);
    width: 100%;
    padding: rem(19) 0 !important;
    margin-top: rem(36);
    text-align: center;
    transition: all 0.5s ease;

    @include responsive(md) {
        width: rem(256);
        margin: rem(61) auto 0;
    }

    &:hover {
        background: $color-text;
        color: #fff;
    }
}

.wpcf7-list-item {
    margin: 0;
    label {
        @include responsive(md) {
            display: flex !important;
            justify-content: center;
            align-items: center;
        }
    }
}

.wpcf7-list-item-label {
    font-family: $base-font-family;
    font-size: rem(14);
    font-weight: 400;
    letter-spacing: 0em;
    line-height: calc(20 / 14);
    padding-left: rem(6);

    @include responsive(md) {
        font-size: rem(14);
        line-height: calc(20.27 / 14);
    }
}

/*==============================================================

==============================================================*/
.wpcf7-form-control-wrap {
    > .wpcf7-not-valid-tip {
        display: none;
        margin-top: 10px;
    }

    &.is-show {
        > .wpcf7-not-valid-tip {
            display: block;
        }
    }
}
