/* hero
----------------------------------------- */
@use "../../global" as *;

/*==============================================================
p-hero
==============================================================*/

/* hero
----------------------------------------- */
.p-hero {
    position: relative;
    margin-top: 6rem;
}
.p-hero figure {
    height: 28rem;
}
.p-hero figure picture img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.p-hero.-faq figure picture img {
    object-position: bottom;
}

.p-hero.-details figure picture img {
    object-position: top;
}

.p-hero {
    @include responsive(lg2) {
        margin-top: rem(118);
    }
}
.p-hero figure {
    @include responsive(lg2) {
        height: rem(339);
    }
}
