/* table
----------------------------------------- */
@use "../global" as *;

.c-table {
    width: 100%;
    margin-top: rem(20);

    @include responsive(md) {
        margin-top: rem(78);
    }

    &__row {
        border-bottom: 1px dashed $color-text;
    }
    &__head {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(12);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(12 / 12);

        @include responsive(md) {
            font-size: rem(16);
            line-height: calc(16 / 16);
        }
    }

    &__data {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(12);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(20 / 12);

        @include responsive(md) {
            font-size: rem(16);
            line-height: calc(24 / 16);
        }
    }

    &__head {
        width: 40%;

        @include responsive(md) {
            width: 23.5%;
        }
    }

    &__data,
    &__head {
        text-align: left;
        padding: rem(16) 0;

        @include responsive(md) {
            padding: rem(33) 0;
        }
    }
}
