/* l-inner
----------------------------------------- */
@use "../global" as *;

.l-inner {
    width: 100%;
    max-width: 1024px;
    margin: 0 auto;
    padding: 0 rem(17);

    @include responsive(sm) {
        padding: 0 rem(47);
    }

    @include responsive(lg2) {
        padding: 0 rem(17);
    }
}

.p-home-subCatch {
    .l-inner {
        padding: 0;
    }
}

.p-home-staff {
    .l-inner {
        padding-top: rem(90);

        @include responsive(lg2) {
            padding: rem(156) rem(33) 0 rem(33);
        }
    }
}

.p-home-benefit {
    .l-inner {
        padding-top: rem(90);
        padding-bottom: rem(190);

        @include responsive(lg2) {
            padding-top: rem(148);
            padding-bottom: rem(180);
        }
    }
}

.p-home-blog {
    .l-inner {
        padding-top: rem(76);
        padding-bottom: rem(10);

        @include responsive(lg2) {
            padding: rem(83) rem(34);
        }
    }
}

.p-home-recruit {
    .l-inner {
        padding-top: rem(78);
        padding-bottom: rem(88);

        @include responsive(lg2) {
            padding: rem(83) 0 rem(138) 0;
        }
    }
}
