/* home-staff
----------------------------------------- */
@use "../../global" as *;

.p-home-staff {
    overflow: hidden;

    &__slider {
        padding-bottom: rem(128);
        margin-top: rem(50);
        transform: translateX(-60px);

        @include responsive(lg2) {
            padding-bottom: rem(150);
            margin-top: rem(52);
        }
    }

    &__item {
        width: rem(246) !important;
        height: rem(311) !important;
        position: relative;

        @include responsive(lg2) {
            width: rem(300) !important;
            height: rem(379) !important;
        }

        &:nth-child(odd) {
            transform: translateY(38px);
        }
    }

    &__textBox {
        position: absolute;
        top: rem(263);
        left: 0;

        @include responsive(lg2) {
            top: rem(336);
        }
    }

    &__comment {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(16);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(16 / 16);

        @include responsive(lg2) {
            font-size: rem(20);
        }

        p {
            display: inline-block;
            background: $color-sub;
            padding: rem(7);
        }

        p:nth-child(2) {
            margin-top: rem(8);

            @include responsive(lg2) {
                margin-top: rem(10);
            }
        }
    }

    &__title {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(12);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(12 / 12);
        margin-top: rem(10);

        @include responsive(lg2) {
            font-size: rem(16);
            margin-top: rem(5);
        }
    }

    &__name {
        font-family: $base-font-family;
        font-size: rem(20);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(20 / 20);
        margin-top: rem(14);

        @include responsive(lg2) {
            font-size: rem(24);
            margin-top: rem(16);
        }
    }
}

/*==============================================================
Splide
==============================================================*/
.p-home-staff {
    .splide {
        visibility: visible;
    }
    .splide__track {
        overflow: visible !important;

        @include responsive(md) {
            padding-left: 5.8% !important;
        }
    }
}

/*==============================================================
Splidearrow
==============================================================*/
.button {
    width: rem(59) !important;
    height: rem(59) !important;
    transition: 0.2s !important;
}

.splide__arrows {
    position: absolute;
    top: rem(626);
    right: 0;
}

.splide__arrow {
    background: #fff !important;
    border: 1px solid $color-text !important;
    opacity: 1 !important;
}

.splide__arrow--next {
    position: absolute;
    right: rem(-74) !important;
}

.splide__arrow--prev {
    position: absolute;
    left: rem(-63) !important;
}

.arrow {
    display: block;
    width: 100%;
    height: 100%;
    position: relative;

    &::before {
        content: "";
        display: block;
        left: 3px;
        width: 16px;
        height: 2px;
        background: $color-text;
        position: absolute;
        top: 50%;
        left: 48%;
        transform: translate(-50%, -48%);
    }
    &::after {
        content: "";
        display: block;
        left: 3px;
        width: 12px;
        height: 12px;
        border-top: 2px solid $color-text;
        border-right: 2px solid $color-text;
        border-radius: 2px;
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%) rotate(45deg);
        transform: translate(-50%, -50%) rotate(45deg);
    }

    &.-prev::after {
        left: 45%;
        -webkit-transform: translate(-50%, -50%) rotate(225deg);
        transform: translate(-50%, -50%) rotate(225deg);
    }
}
