/* staff
----------------------------------------- */
@use "../../global" as *;

/*==============================================================
p-staff
==============================================================*/
.p-staff {
    &__list {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include responsive(sm) {
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: space-around;
        }
    }

    &__item {
        position: relative;
        max-width: 300px;

        @include responsive(sm) {
            width: 45%;
        }

        @include responsive(md) {
            width: 30% !important;
        }
        &:not(:first-of-type) {
            margin-top: rem(110);

            @include responsive(sm) {
                margin-top: 0;
            }
        }

        &:nth-of-type(n + 3) {
            @include responsive(sm) {
                margin-top: rem(100);
            }

            @include responsive(md) {
                margin-top: 0 !important;
            }
        }

        &:nth-of-type(n + 4) {
            @include responsive(md) {
                margin-top: rem(139) !important;
            }
        }
    }

    &__textBox {
        position: absolute;
        bottom: rem(-70);
        left: 0;

        @include responsive(sm) {
            bottom: rem(-70);
        }

        @include responsive(lg2) {
            top: rem(332) !important;
        }
    }

    &__comment {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(16);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(16 / 16);

        @include responsive(lg2) {
            font-size: rem(20);
        }

        p {
            display: inline-block;
            background: $color-sub;
            padding: rem(5) rem(7);
        }

        p:nth-child(2) {
            margin-top: rem(5);
        }
    }

    &__title {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(12);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(12 / 12);
        margin-top: rem(10);

        @include responsive(lg2) {
            font-size: rem(16);
            margin-top: rem(16);
        }

        &--pl {
            padding-left: rem(5);
        }
    }

    &__name {
        font-family: $base-font-family;
        font-size: rem(20);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(20 / 20);
        margin-top: rem(14);

        @include responsive(lg2) {
            font-size: rem(24);
            margin-top: rem(16);
        }
    }
}

.p-staff-member {
    &__img {
        figure {
            picture {
                img {
                    width: rem(300);
                    max-width: 300px;
                    height: rem(379);
                    object-fit: cover;
                }
            }
        }
    }
}
